import { React, useState, useEffect } from 'react';
import * as S from './BoardForm.style';
import Dropzone from 'react-dropzone';
import { useCreateBoard, useUpdateBoard } from '../../API/BoardAPi';
import { useGetDetailBoard } from '../../API/BoardAPi';
import { useAtom, useAtomValue } from 'jotai';
import { BOARD_PATH } from '../common/path';
import { useParams } from 'react-router-dom';
import { selectedBoardAtom } from '../../Atoms/BoardAtom';

const BoardForm = ({ onPageChange }) => {
  const [select, setSelect] = useAtom(selectedBoardAtom);
  const [title, setTitle] = useState('');
  const [content, setContent] = useState('');
  const [image, setImage] = useState('');
  const [status, setStatus] = useState('PUBLIC');

  const { mutateAsync: createPost, isLoading: isCreating } = useCreateBoard(); //     mutate  !
  const { mutateAsync: updatePost, isLoading: isUpdating } = useUpdateBoard();
  //  mutate: createPost  mutate   ?
  const handleSubmit = async (e) => {
    //react-query
    e.preventDefault();
    try {
      const formData = new FormData();
      formData.append('title', title); //  ,  
      formData.append('content', content);
      formData.append('image', image);
      formData.append('status', status);
      for (const pair of formData.entries()) {
        console.log(pair[0] + ': ' + pair[1]);
      }
      if (select && select.id) {
        // selectedPost   id    
        await updatePost({ id: select.id, updatedPost: formData });
      } else {
        // =>  .
        await createPost(formData);
      }
    } catch (error) {
      console.log(error);
    }
    alert(' !');
    setSelect('');
    onPageChange(BOARD_PATH);
  };
  const handlImageChange = (event) => {
    setImage(event.target.files[0]);
  };
  const handleDrop = (acceptedFiles) => {
    //   
    //    state 
    const selectedImage = acceptedFiles[0];
    setImage(selectedImage);
  };

  useEffect(() => {
    //       
    if (select) {
      setTitle(select.title);
      setContent(select.content);
      if (select.image) {
        setImage(select.image);
      } else {
        setImage('');
      }
    }
  }, [select]);

  return (
    <S.Container>
      <S.FormContainer>
        <form onSubmit={handleSubmit}>
          <S.CheckBox
            value={status}
            onChange={(e) => setStatus(e.target.value)}>
            <option value='PUBLIC'></option>
            <option value='PRIVATE'></option>
          </S.CheckBox>
          <div>
            <label></label>
            <input
              type='text'
              id='title'
              value={title}
              onChange={(e) => setTitle(e.target.value)}
            />
          </div>
          <div>
            <label className='content'></label>
            <textarea
              className='textarea'
              id='content'
              value={content}
              onChange={(e) => setContent(e.target.value)}
              style={{ whiteSpace: 'pre-line' }}
            />
          </div>
          <div>
            <label></label>
            {image ? (
              <S.ImageContainer>
                <img
                  src={
                    typeof image === 'string' //   src  .
                      ? image
                      : URL.createObjectURL(image)
                  }
                  alt='Example'
                  onClick={() => {
                    document.getElementById('imageInput').click();
                  }}
                />
                <input //input         !
                  id='imageInput'
                  type='file'
                  accept='image/*' //  
                  onChange={handlImageChange}
                  style={{ display: 'none' }}
                />
              </S.ImageContainer>
            ) : (
              <Dropzone onDrop={handleDrop} accept='image/*'>
                {({ getRootProps, getInputProps }) => (
                  <div {...getRootProps()}>
                    <input {...getInputProps()} />
                    <p className='DropBorder'>
                           !
                    </p>
                  </div>
                )}
              </Dropzone>
            )}
          </div>
          <button type='submit' disabled={isCreating || isUpdating}>
            {isCreating || isUpdating ? ' ...' : ''}
          </button>
          {/*      ...   +   */}
        </form>
      </S.FormContainer>
    </S.Container>
  );
};

export default BoardForm;
