//
export const useCreateLike = (boardId) => {
  const queryClient = useQueryClient();
  return useMutation(
    async (newPost) => {
      const { data } = await serverWithToken.post(
        `/board/likes/${boardId}`,
        newPost
      );
      return data;
    },
    {
      onSuccess: () => {
        queryClient.invalidateQueries(['BoardList']); //  BoardList useQuery key?
      }, //    (invalidate),       ..
    }
  );
};

export const useGetLike = () => {
  const { isLoggedIn } = Auth();

  return useQuery(['like'], async () => {
    if (!isLoggedIn) {
      return null; //   ,  (null) .
    }

    const { data } = await serverWithToken.get('/board/likes');
    return data;
  });
};
